 from 'dashboard/components/ui/Banner.vue';\n\n// stores and apis\nimport { mapGetters } from 'vuex';\n\n// mixins\nimport inboxMixin, { INBOX_FEATURES } from 'shared/mixins/inboxMixin';\n\n// utils\nimport { getTypingUsersText } from '../../../helper/commons';\nimport { calculateScrollTop } from './helpers/scrollTopCalculationHelper';\nimport { LocalStorage } from 'shared/helpers/localStorage';\nimport {\n  filterDuplicateSourceMessages,\n  getReadMessages,\n  getUnreadMessages,\n} from 'dashboard/helper/conversationHelper';\n\n// constants\nimport { BUS_EVENTS } from 'shared/constants/busEvents';\nimport { REPLY_POLICY } from 'shared/constants/links';\nimport wootConstants from 'dashboard/constants/globals';\nimport { LOCAL_STORAGE_KEYS } from 'dashboard/constants/localStorage';\n\nexport default {\n  components: {\n    Message,\n    ReplyBox,\n    Banner,\n    ConversationLabelSuggestion,\n  },\n  mixins: [inboxMixin],\n  props: {\n    isContactPanelOpen: {\n      type: Boolean,\n      default: false,\n    },\n    isInboxView: {\n      type: Boolean,\n      default: false,\n    },\n  },\n  setup() {\n    const isPopOutReplyBox = ref(false);\n    const { isEnterprise } = useConfig();\n\n    const closePopOutReplyBox = () => {\n      isPopOutReplyBox.value = false;\n    };\n\n    const showPopOutReplyBox = () => {\n      isPopOutReplyBox.value = !isPopOutReplyBox.value;\n    };\n\n    const keyboardEvents = {\n      Escape: {\n        action: closePopOutReplyBox,\n      },\n    };\n\n    useKeyboardEvents(keyboardEvents);\n\n    const {\n      isAIIntegrationEnabled,\n      isLabelSuggestionFeatureEnabled,\n      fetchIntegrationsIfRequired,\n      fetchLabelSuggestions,\n    } = useAI();\n\n    return {\n      isEnterprise,\n      isPopOutReplyBox,\n      closePopOutReplyBox,\n      showPopOutReplyBox,\n      isAIIntegrationEnabled,\n      isLabelSuggestionFeatureEnabled,\n      fetchIntegrationsIfRequired,\n      fetchLabelSuggestions,\n    };\n  },\n  data() {\n    return {\n      isLoadingPrevious: true,\n      heightBeforeLoad: null,\n      conversationPanel: null,\n      hasUserScrolled: false,\n      isProgrammaticScroll: false,\n      messageSentSinceOpened: false,\n      labelSuggestions: [],\n    };\n  },\n\n  computed: {\n    ...mapGetters({\n      currentChat: 'getSelectedChat',\n      listLoadingStatus: 'getAllMessagesLoaded',\n      currentAccountId: 'getCurrentAccountId',\n      globalConfig: 'globalConfig/get',\n    }),\n    isOpen() {\n      return this.currentChat?.status === wootConstants.STATUS_TYPE.OPEN;\n    },\n    shouldShowLabelSuggestions() {\n      return (\n        this.isOpen &&\n        this.isEnterprise &&\n        this.isAIIntegrationEnabled &&\n        !this.messageSentSinceOpened\n      );\n    },\n    inboxId() {\n      return this.currentChat.inbox_id;\n    },\n    inbox() {\n      return this.$store.getters['inboxes/getInbox'](this.inboxId);\n    },\n    typingUsersList() {\n      const userList = this.$store.getters[\n        'conversationTypingStatus/getUserList'\n      ](this.currentChat.id);\n      return userList;\n    },\n    isAnyoneTyping() {\n      const userList = this.typingUsersList;\n      return userList.length !== 0;\n    },\n    typingUserNames() {\n      const userList = this.typingUsersList;\n\n      if (this.isAnyoneTyping) {\n        const userListAsName = getTypingUsersText(userList);\n        return userListAsName;\n      }\n\n      return '';\n    },\n    getMessages() {\n      const messages = this.currentChat.messages || [];\n      if (this.isAWhatsAppChannel) {\n        return filterDuplicateSourceMessages(messages);\n      }\n      return messages;\n    },\n    readMessages() {\n      return getReadMessages(\n        this.getMessages,\n        this.currentChat.agent_last_seen_at\n      );\n    },\n    unReadMessages() {\n      return getUnreadMessages(\n        this.getMessages,\n        this.currentChat.agent_last_seen_at\n      );\n    },\n    shouldShowSpinner() {\n      return (\n        (this.currentChat && this.currentChat.dataFetched === undefined) ||\n        (!this.listLoadingStatus && this.isLoadingPrevious)\n      );\n    },\n    conversationType() {\n      const { additional_attributes: additionalAttributes } = this.currentChat;\n      const type = additionalAttributes ? additionalAttributes.type : '';\n      return type || '';\n    },\n\n    isATweet() {\n      return this.conversationType === 'tweet';\n    },\n    isRightOrLeftIcon() {\n      if (this.isContactPanelOpen) {\n        return 'arrow-chevron-right';\n      }\n      return 'arrow-chevron-left';\n    },\n    getLastSeenAt() {\n      const { contact_last_seen_at: contactLastSeenAt } = this.currentChat;\n      return contactLastSeenAt;\n    },\n\n    replyWindowBannerMessage() {\n      if (this.isAWhatsAppChannel) {\n        return this.$t('CONVERSATION.TWILIO_WHATSAPP_CAN_REPLY');\n      }\n      if (this.isAPIInbox) {\n        const { additional_attributes: additionalAttributes = {} } = this.inbox;\n        if (additionalAttributes) {\n          const {\n            agent_reply_time_window_message: agentReplyTimeWindowMessage,\n          } = additionalAttributes;\n          return agentReplyTimeWindowMessage;\n        }\n        return '';\n      }\n      return this.$t('CONVERSATION.CANNOT_REPLY');\n    },\n    replyWindowLink() {\n      if (this.isAWhatsAppChannel) {\n        return REPLY_POLICY.FACEBOOK;\n      }\n      if (!this.isAPIInbox) {\n        return REPLY_POLICY.TWILIO_WHATSAPP;\n      }\n      return '';\n    },\n    replyWindowLinkText() {\n      if (this.isAWhatsAppChannel) {\n        return this.$t('CONVERSATION.24_HOURS_WINDOW');\n      }\n      if (!this.isAPIInbox) {\n        return this.$t('CONVERSATION.TWILIO_WHATSAPP_24_HOURS_WINDOW');\n      }\n      return '';\n    },\n    unreadMessageCount() {\n      return this.currentChat.unread_count || 0;\n    },\n    isInstagramDM() {\n      return this.conversationType === 'instagram_direct_message';\n    },\n    inboxSupportsReplyTo() {\n      const incoming = this.inboxHasFeature(INBOX_FEATURES.REPLY_TO);\n      const outgoing =\n        this.inboxHasFeature(INBOX_FEATURES.REPLY_TO_OUTGOING) &&\n        !this.is360DialogWhatsAppChannel;\n\n      return { incoming, outgoing };\n    },\n  },\n\n  watch: {\n    currentChat(newChat, oldChat) {\n      if (newChat.id === oldChat.id) {\n        return;\n      }\n      this.fetchAllAttachmentsFromCurrentChat();\n      this.fetchSuggestions();\n      this.messageSentSinceOpened = false;\n    },\n  },\n\n  created() {\n    this.$emitter.on(BUS_EVENTS.SCROLL_TO_MESSAGE, this.onScrollToMessage);\n    // when a new message comes in, we refetch the label suggestions\n    this.$emitter.on(BUS_EVENTS.FETCH_LABEL_SUGGESTIONS, this.fetchSuggestions);\n    // when a message is sent we set the flag to true this hides the label suggestions,\n    // until the chat is changed and the flag is reset in the watch for currentChat\n    this.$emitter.on(BUS_EVENTS.MESSAGE_SENT, () => {\n      this.messageSentSinceOpened = true;\n    });\n  },\n\n  mounted() {\n    this.addScrollListener();\n    this.fetchAllAttachmentsFromCurrentChat();\n    this.fetchSuggestions();\n  },\n\n  beforeDestroy() {\n    this.removeBusListeners();\n    this.removeScrollListener();\n  },\n\n  methods: {\n    async fetchSuggestions() {\n      // start empty, this ensures that the label suggestions are not shown\n      this.labelSuggestions = [];\n\n      if (this.isLabelSuggestionDismissed()) {\n        return;\n      }\n\n      if (!this.isEnterprise) {\n        return;\n      }\n\n      // method available in mixin, need to ensure that integrations are present\n      await this.fetchIntegrationsIfRequired();\n\n      if (!this.isLabelSuggestionFeatureEnabled) {\n        return;\n      }\n\n      this.labelSuggestions = await this.fetchLabelSuggestions({\n        conversationId: this.currentChat.id,\n      });\n\n      // once the labels are fetched, we need to scroll to bottom\n      // but we need to wait for the DOM to be updated\n      // so we use the nextTick method\n      this.$nextTick(() => {\n        // this param is added to route, telling the UI to navigate to the message\n        // it is triggered by the SCROLL_TO_MESSAGE method\n        // see setActiveChat on ConversationView.vue for more info\n        const { messageId } = this.$route.query;\n\n        // only trigger the scroll to bottom if the user has not scrolled\n        // and there's no active messageId that is selected in view\n        if (!messageId && !this.hasUserScrolled) {\n          this.scrollToBottom();\n        }\n      });\n    },\n    isLabelSuggestionDismissed() {\n      return LocalStorage.getFlag(\n        LOCAL_STORAGE_KEYS.DISMISSED_LABEL_SUGGESTIONS,\n        this.currentAccountId,\n        this.currentChat.id\n      );\n    },\n    fetchAllAttachmentsFromCurrentChat() {\n      this.$store.dispatch('fetchAllAttachments', this.currentChat.id);\n    },\n    removeBusListeners() {\n      this.$emitter.off(BUS_EVENTS.SCROLL_TO_MESSAGE, this.onScrollToMessage);\n    },\n    onScrollToMessage({ messageId = '' } = {}) {\n      this.$nextTick(() => {\n        const messageElement = document.getElementById('message' + messageId);\n        if (messageElement) {\n          this.isProgrammaticScroll = true;\n          messageElement.scrollIntoView({ behavior: 'smooth' });\n          this.fetchPreviousMessages();\n          messageElement.classList.add('has-bg');\n          const HIGHLIGHT_TIMER = 2000;\n          setTimeout(() => {\n            messageElement.classList.remove('has-bg');\n          }, HIGHLIGHT_TIMER);\n        } else {\n          this.scrollToBottom();\n        }\n      });\n      this.makeMessagesRead();\n    },\n    addScrollListener() {\n      this.conversationPanel = this.$el.querySelector('.conversation-panel');\n      this.setScrollParams();\n      this.conversationPanel.addEventListener('scroll', this.handleScroll);\n      this.$nextTick(() => this.scrollToBottom());\n      this.isLoadingPrevious = false;\n    },\n    removeScrollListener() {\n      this.conversationPanel.removeEventListener('scroll', this.handleScroll);\n    },\n    scrollToBottom() {\n      this.isProgrammaticScroll = true;\n      let relevantMessages = [];\n\n      // label suggestions are not part of the messages list\n      // so we need to handle them separately\n      let labelSuggestions =\n        this.conversationPanel.querySelector('.label-suggestion');\n\n      // if there are unread messages, scroll to the first unread message\n      if (this.unreadMessageCount > 0) {\n        // capturing only the unread messages\n        relevantMessages =\n          this.conversationPanel.querySelectorAll('.message--unread');\n      } else if (labelSuggestions) {\n        // when scrolling to the bottom, the label suggestions is below the last message\n        // so we scroll there if there are no unread messages\n        // Unread messages always take the highest priority\n        relevantMessages = [labelSuggestions];\n      } else {\n        // if there are no unread messages or label suggestion, scroll to the last message\n        // capturing last message from the messages list\n        relevantMessages = Array.from(\n          this.conversationPanel.querySelectorAll('.message--read')\n        ).slice(-1);\n      }\n\n      this.conversationPanel.scrollTop = calculateScrollTop(\n        this.conversationPanel.scrollHeight,\n        this.$el.scrollHeight,\n        relevantMessages\n      );\n    },\n    onToggleContactPanel() {\n      this.$emit('contactPanelToggle');\n    },\n    setScrollParams() {\n      this.heightBeforeLoad = this.conversationPanel.scrollHeight;\n      this.scrollTopBeforeLoad = this.conversationPanel.scrollTop;\n    },\n\n    async fetchPreviousMessages(scrollTop = 0) {\n      this.setScrollParams();\n      const shouldLoadMoreMessages =\n        this.currentChat.dataFetched === true &&\n        !this.listLoadingStatus &&\n        !this.isLoadingPrevious;\n\n      if (\n        scrollTop < 100 &&\n        !this.isLoadingPrevious &&\n        shouldLoadMoreMessages\n      ) {\n        this.isLoadingPrevious = true;\n        try {\n          await this.$store.dispatch('fetchPreviousMessages', {\n            conversationId: this.currentChat.id,\n            before: this.currentChat.messages[0].id,\n          });\n          const heightDifference =\n            this.conversationPanel.scrollHeight - this.heightBeforeLoad;\n          this.conversationPanel.scrollTop =\n            this.scrollTopBeforeLoad + heightDifference;\n          this.setScrollParams();\n        } catch (error) {\n          // Ignore Error\n        } finally {\n          this.isLoadingPrevious = false;\n        }\n      }\n    },\n\n    handleScroll(e) {\n      if (this.isProgrammaticScroll) {\n        // Reset the flag\n        this.isProgrammaticScroll = false;\n        this.hasUserScrolled = false;\n      } else {\n        this.hasUserScrolled = true;\n      }\n      this.$emitter.emit(BUS_EVENTS.ON_MESSAGE_LIST_SCROLL);\n      this.fetchPreviousMessages(e.target.scrollTop);\n    },\n\n    makeMessagesRead() {\n      this.$store.dispatch('markMessagesRead', { id: this.currentChat.id });\n    },\n\n    async getInReplyToMessage(parentMessage) {\n      if (!parentMessage) return {};\n      const inReplyToMessageId = parentMessage.content_attributes?.in_reply_to;\n      if (!inReplyToMessageId) return {};\n\n      let replyToMessage = this.currentChat?.messages.find(message => {\n        if (message.id === inReplyToMessageId) {\n          return true;\n        }\n        return false;\n      });\n      if (!replyToMessage) {\n        const params = {\n          conversationId: this.currentChat.id,\n          after: inReplyToMessageId - 1,\n          before: inReplyToMessageId + 1,\n        };\n        const {\n          data: { payload },\n        } = await MessageApi.getPreviousMessages(params);\n        replyToMessage = payload[0];\n      }\n      return replyToMessage;\n    },\n  },\n};\n</script>\n\n<template>\n  <div class=\"flex flex-col justify-between flex-grow h-full min-w-0 m-0\">\n    <Banner\n      v-if=\"!currentChat.can_reply\"\n      color-scheme=\"alert\"\n      :banner-message=\"replyWindowBannerMessage\"\n      :href-link=\"replyWindowLink\"\n      :href-link-text=\"replyWindowLinkText\"\n    />\n    <div class=\"flex justify-end\">\n      <woot-button\n        variant=\"smooth\"\n        size=\"tiny\"\n        color-scheme=\"secondary\"\n        class=\"box-border fixed z-10 bg-white border border-r-0 border-solid rounded-bl-calc rtl:rotate-180 rounded-tl-calc dark:bg-slate-700 border-slate-50 dark:border-slate-600\"\n        :class=\"\n          isInboxView ? 'top-52 md:top-40' : 'top-[9.5rem] md:top-[6.25rem]'\n        \"\n        :icon=\"isRightOrLeftIcon\"\n        @click=\"onToggleContactPanel\"\n      />\n    </div>\n    <ul class=\"conversation-panel\" :style=\"globalConfig.conversationStyleCss\">\n      <transition name=\"slide-up\">\n        <li class=\"min-h-[4rem]\">\n          <span v-if=\"shouldShowSpinner\" class=\"spinner message\" />\n        </li>\n      </transition>\n      <Message\n        v-for=\"message in readMessages\"\n        :key=\"message.id\"\n        class=\"message--read ph-no-capture\"\n        data-clarity-mask=\"True\"\n        :data=\"message\"\n        :is-a-tweet=\"isATweet\"\n        :is-a-whatsapp-channel=\"isAWhatsAppChannel\"\n        :is-web-widget-inbox=\"isAWebWidgetInbox\"\n        :is-a-facebook-inbox=\"isAFacebookInbox\"\n        :is-an-email-inbox=\"isAnEmailChannel\"\n        :is-instagram=\"isInstagramDM\"\n        :inbox-supports-reply-to=\"inboxSupportsReplyTo\"\n        :in-reply-to=\"getInReplyToMessage(message)\"\n      />\n      <li v-show=\"unreadMessageCount != 0\" class=\"unread--toast\">\n        <span>\n          {{ unreadMessageCount > 9 ? '9+' : unreadMessageCount }}\n          {{\n            unreadMessageCount > 1\n              ? $t('CONVERSATION.UNREAD_MESSAGES')\n              : $t('CONVERSATION.UNREAD_MESSAGE')\n          }}\n        </span>\n      </li>\n      <Message\n        v-for=\"message in unReadMessages\"\n        :key=\"message.id\"\n        class=\"message--unread ph-no-capture\"\n        data-clarity-mask=\"True\"\n        :data=\"message\"\n        :is-a-tweet=\"isATweet\"\n        :is-a-whatsapp-channel=\"isAWhatsAppChannel\"\n        :is-web-widget-inbox=\"isAWebWidgetInbox\"\n        :is-a-facebook-inbox=\"isAFacebookInbox\"\n        :is-instagram-dm=\"isInstagramDM\"\n        :inbox-supports-reply-to=\"inboxSupportsReplyTo\"\n        :in-reply-to=\"getInReplyToMessage(message)\"\n      />\n      <ConversationLabelSuggestion\n        v-if=\"shouldShowLabelSuggestions\"\n        :suggested-labels=\"labelSuggestions\"\n        :chat-labels=\"currentChat.labels\"\n        :conversation-id=\"currentChat.id\"\n      />\n    </ul>\n    <div\n      class=\"conversation-footer\"\n      :class=\"{ 'modal-mask': isPopOutReplyBox }\"\n    >\n      <div\n        v-if=\"isAnyoneTyping\"\n        class=\"absolute flex items-center w-full h-0 -top-7\"\n      >\n        <div\n          class=\"flex py-2 pr-4 pl-5 shadow-md rounded-full bg-white dark:bg-slate-700 text-slate-800 dark:text-slate-100 text-xs font-semibold my-2.5 mx-auto\"\n        >\n          {{ typingUserNames }}\n          <img\n            class=\"w-6 ltr:ml-2 rtl:mr-2\"\n            src=\"~dashboard/assets/images/typing.gif\"\n            alt=\"Someone is typing\"\n          />\n        </div>\n      </div>\n      <ReplyBox\n        :conversation-id=\"currentChat.id\"\n        :popout-reply-box.sync=\"isPopOutReplyBox\"\n        @click=\"showPopOutReplyBox\"\n      />\n    </div>\n  </div>\n</template>\n\n<style scoped>\n@tailwind components;\n@layer components {\n  .rounded-bl-calc {\n    border-bottom-left-radius: calc(1.5rem + 1px);\n  }\n\n  .rounded-tl-calc {\n    border-top-left-radius: calc(1.5rem + 1px);\n  }\n}\n</style>\n\n<style scoped lang=\"scss\">\n.modal-mask {\n  @apply absolute;\n\n  &::v-deep {\n    .ProseMirror-woot-style {\n      @apply max-h-[25rem];\n    }\n\n    .reply-box {\n      @apply border border-solid border-slate-75 dark:border-slate-600 max-w-[75rem] w-[70%];\n    }\n\n    .reply-box .reply-box__top {\n      @apply relative min-h-[27.5rem];\n    }\n\n    .reply-box__top .input {\n      @apply min-h-[27.5rem];\n    }\n\n    .emoji-dialog {\n      @apply absolute left-auto bottom-1;\n    }\n  }\n}\n</style>\n"]}]);
